"use client";

import { Button } from "@/components/ui/button";

type Lesson = {
  title: string;
  duration: number;
};
export default function Counter() {
  const lesson: Lesson = { title: "TipeScript", duration: 120 };
  const count = calculateSum(5, 10);

  function calculateSum(a: number, b: number) {
    return a + b;
  }

  function handleClick() {
    console.log(count);
  }

  return (
    <div className="flex flex-col gap-4">
      <CoolCount count={count} />
      <Button onClick={handleClick} className="w-fit">
        {" "}
        Увеличить
      </Button>
    </div>
  );
}
type Props = {
  count: number;
};

function CoolCount({ count }: Props) {
  return <span className="text-#red-500">{count}</span>;
}
